
<script type="text/javascript">

  import { mapGetters, mapActions} from 'vuex'

  export default {
  name: 'selfTile',
  data(){
    return {
      firstPlayer:false,
      a:'0',
      yourTiles:[],
      memory:'',
      tileMemory:{},
      meldedTiles: []
    }
  },
  computed: {
    ...mapGetters ([
   'getTiles'
    ]),
},
  methods: {
    ...mapActions([
    'tileChosen',
    'updMeldedTile'
  ]),
    action: function (b){
      if (!this.memory){
      this.memory = b;
      this.tileMemory = this.getTiles(this.a)[this.memory]
      return
    }
      if (this.memory!==b){
      this.getTiles(this.a).splice(this.memory, 1);
      this.getTiles(this.a).splice(b, 0, this.tileMemory);
      this.memory = '';
      this.tileMemory = {};
      return
    }
      if (this.memory === b){
        let temp = []
      temp[0] = this.getTiles(this.a)[this.memory];
      this.getTiles(this.a).splice(this.memory, 1);
      temp[1]=this.a
      this.updMeldedTile(temp);
      this.memory='';
      this.tileMemory={}
      return
    }
  },
    tileClicked: function (a, id){
      let arg = [a, id]
      this.tileChosen(arg)
    }
}
}
</script>
<template >
  <div id="yourTiles" >

    <button>
    <img v-for="(tile, index) in getTiles(`${a}`)"
        :key="index"
        :src="require(`../assets/${tile.url}.png`)"
        :alt="`${tile.url}`"
        v-on:click = "action(`${index}`); tileClicked(`${a}`,`${tile.id}`)"
        />
       <!-- :class = "{'bigger':tile.chosen}" /> -->
       <!-- `${tile.chosen}` -->
    </button>
  </div>
</template>
 <style scoped>
  img {
    height: 15vh;
  }
  button{
    border: 0px;
    background-color: #004455;
  }
</style>
