<template >
  <div height='100vh'>
  <div id="begin" v-if="counter===0">
    <img :src="require(`../assets/${dicevalue}.png`)" alt="Image lost..." v-on:click="action"/>
  </div>
  <div id="sit_select" v-else-if="counter===1">
    <img src="../assets/east.png" alt="Direction" v-on:click="action()"/>
  </div>
  <div id="casting" v-else-if="counter===2">
    <svg class="table" width="60vw" height="50vh" style="background-color:rgb(225, 220, 200)">RED
    </svg>
</div>

  </div>
</template>
<script type="text/javascript">
  import {mapActions} from 'vuex';

  export default {
  name: 'center',
  data () {
    return{
      dicevalue: 'six_points',
      counter: 0,
      dice: ['one_point', 'two_points', 'three_points', 'four_points', 'five_points', 'six_points']
  }
  },
    methods: {
      ...mapActions([
          'startTiles']
          ),
      action: function(){this.dicevalue = this.dice[this.getRandomizer(0, 6)]},
      action1: function(){return
      },
      getRandomizer: function (bottom, top){
        let number
        for(let i=0; i<5000; i++){
        number = Math.floor( Math.random() * ( 1 + top - bottom ) ) + bottom;
      } return number
      }
    },
    created() {
      this.startTiles();
    }
}
</script>

