// import {yourStartTiles} from 'majiangArray'

const state = {
  tilesAll:[],
  tiles: [],
  meldedTiles: [
    [],[],[],[]
  ],
  publicTiles: [],
};

const actions = {
  startTiles(context) {
    context.commit('allTiles')
    context.commit('setTiles');
    context.commit('setPublicTiles')
  },
  tileChosen({commit}, arg){
     commit('tileChosen', arg)
  },
  updMeldedTile({commit}, arg){
     commit('tileMelded', arg)
  }
};

const mutations = {
  allTiles: state=>state.tilesAll = tileMaker(),
  setTiles: state=>state.tiles = yourStartTiles(),
  setPublicTiles: state=>state.publicTiles = publicWall(state.tilesAll),
  tileChosen: (state, arg) =>  state.tiles[arg[0]].filter(
    function (e) {
      if (e.id === arg[1]) {e.chosen = !e.chosen}
    }),
  tileMelded: (state, arg) => state.meldedTiles[arg[1]].push(arg[0])
};
    // meldedTiles: state=>state.meldedTiles=tileMaker(),
const getters = {
  getTiles: state => index => state.tiles[index],
  // getPublicTiles: state=>state.publicTiles,
  getPublicTiles: state => state.publicTiles,
  getAllTiles: state => state.tilesAll,
  getMeldedTiles: state => index => state.meldedTiles[index],
}
export default {
      state,
      getters,
      actions,
      mutations
  };


  // function to generate collection of tiles
  function tileMaker (){
    let id=0
    let tiles = []
    // let publicTiles = []
    let suites_name = ['tiao', 'wan', 'circle']
    let aName=[]
    let season=['spring', 'summer', 'fall', 'winter']
    let flower=['winter_sweet', 'archidaceae', 'bamboo', 'chrysanthemum']
    let dragon=['red_zhoung', 'facai', 'baiban']
    let wind=['east_wind', 'south_wind', 'west_wind', 'north_wind']
    let order=['one', 'two', 'three', 'four', 'five', 'six', 'seven', 'eight', 'nine']
     for(let i = 0; i < 9; i++){
        for(let j =0; j < 4; j++){
          if (i<3){tiles.push({url:dragon[i], chosen:false, id:id++})}
          if(i<4){tiles.push({url:wind[i], chosen:false, id:id++})}
          for(let k=0; k<3; k++){
           aName[j] = order[i]+"_"+suites_name[k]
           tiles.push({url:aName[j], chosen:false, id:id++})
         }
           if(i==0){
           tiles.push({url:season[j],chosen:false, id:id++})
           tiles.push({url:flower[j],chosen:false, id:id++})
         }
        }
   }
   // window.console.log(id)
   // window.console.log(tiles)
   return tiles
  }

  let tileIndex = []
  // let publicTiles = []
  // let sit = ['East', 'South', 'West', 'North']


  let eastWall=[], southWall=[],westWall=[],northWall=[]
  let yourTiles =[eastWall, southWall, westWall, northWall]
  let tilesNotInWall = []
  let totalTiles = 144
  let wall = tileMaker()

  let player = {userN:"", location:'', firstPlayer:false, userId:0}
  // let players = []
  function yourStartTiles(){
   for (let k = 0; k < 4; k++){
    getSeatTiles(k, player.firstPlayer)
  }
  return yourTiles
  }

  // console.log(yourStartTiles())
  function publicWall(a){

  let publicTiles = []
  a.forEach(myFunction)
  function myFunction(item){
    const isNotIn = (everyid) => everyid !== item.id
  if(tilesNotInWall.every(isNotIn)){
         publicTiles.push(item)
       }
      }

      return publicTiles
    }
  //build a private hands for players
  function getSeatTiles(seat, firstPlayer){
    let x
    (firstPlayer) ? x =2 : x = 1
    for (let k=0; k < x; k++){
    tileIndex = getIndex()
    tilesNotInWall.push(wall[tileIndex].id)
    yourTiles[seat].push(wall[tileIndex])
  }
       for (let i = 0; i < 12; i++){
         tileIndex=getIndex()
         tilesNotInWall.push(tileIndex)
    yourTiles[seat].push(wall[tileIndex])
  }
  // console.log(yourTiles[seat]+ "---"+ sit[seat])
  }

  // making tileIndex
  function getIndex () {
    tileIndex = getRandomizer(1, totalTiles)
    for (let i = 0; i < tilesNotInWall.length; i++){
    if (tilesNotInWall[i]===tileIndex) {getIndex()}
  } return tileIndex
  }

  //function for get a randome number
  function getRandomizer(bottom, top) {
          return Math.floor( Math.random() * ( 1 + top - bottom ) ) + bottom;
      }
